import React, {Component} from 'react';
import FlexibleModal from 'react-modal-touch';

import './App.css';

class App extends Component {
    constructor(props) {
        super(props);

        this.state = {
            modalIsOpen: false
        };
    }

    openModal = () => {
        this.setState({modalIsOpen: true});
    }

    closeModal = () => {
        this.setState({modalIsOpen: false});
    }

    render() {
        const { modalIsOpen } = this.state;

        return (
            <>
                <div className="header">
                    <p className="title">Welcome to Flexible Modal üòª</p>
                    {!modalIsOpen && <button onClick={this.openModal}>Open modal</button>}
                </div>
                <FlexibleModal
                    minWidth={600}
                    minHeight={400}
                    onRequestClose={this.closeModal}
                    isOpen={this.state.modalIsOpen}>
                        <p className="modal-title">Modal ü•∞</p>
                        <div className="body">
                            <p>Use mouse/touch and drag to move the modal üòä</p>
                            <p>Use the right bottom arrow to resize üòç</p>
                        </div>
                        <button onClick={this.closeModal}>Close modal</button>
                </FlexibleModal>
            </>
        );
    }
}

export default App;